.contact-form1{
    padding-top: 200px;
    margin-top: -10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #1a1a1a;
    padding-bottom: 50px;
}
.contact-head-0{
    color: rgb(196, 196, 196);
}
.im-contact1{
    height: 300px;
    width: 300px;
    object-fit: contain;
}
.contact-container1{
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 500px;
    margin-top: 50px;
    border: 1px solid rgb(112, 112, 112);
    margin-bottom: 50px;

}
.contact-container-next{
    display: flex;
    align-items: center;
    padding-left: 50px;
    padding-right: 50px;
}
.input-field-1{
    color: white;
    background-color: #1a1a1a;
}
.contact-containter-left{
    display: flex;
    align-items: center;
    place-content: center;
    padding-left: 50px;
    padding-right: 50px;
}
.contact-head-1{
    margin-top: 10px;
    color: #ca9400;
    font-weight: 700;
}
@media (max-width: 480px) {
    .contact-head-1{
        font-size: 30px;
        font-weight: 700;
    }
    .contact-head-0{
        padding-left: 30px;
        padding-right: 20px;
    }
    .contact-container1{
        grid-template-columns: 1fr;
        width: 350px;
        padding: 0;
        margin: 10px;
        padding-bottom: 20px;
        height: auto;
        border: none;
    }
    .input-box{
        margin-top: 20px;
        width: 250px;
    }
    .im-contact1{
        height: 0px;
        width: 0px;
        object-fit: contain;
    }

}