.maps-container{
    background-color: #1a1a1a;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 100px;
}
.maps-left{
    display: flex;
    flex-direction: column;
}
.maps-next{
    display: flex;
    place-content: center;
}
.map-location{
    height: 430px;
    width: 450px;
    margin: 10px;
    color: #1a1a1a;
    background-color: #1a1a1a;
}
.back-color{
    background-color: #1a1a1a;
}
@media (max-width: 480px) {
    .map-location{
        width: 320px;
        height: 300px;
    }
    .maps-container{
        grid-template-columns: 1fr;
        padding-left: 10px;
        padding-right: 10px;
    }
    .maps-left{
        padding-left: 5%;
        padding-right: 5%;
        
    }
}
.more-links{
    display: grid;
    grid-template-columns: 120px 120px 120px;
    width: 100%;
    background-color: #1a1a1a;
    place-content: center;
}
.social-rect{
    border-radius: 5px;
    display: flex;
    height: 120px;
    width: 100px;
    background-color: #ca9400;
    align-items: center;
    place-content: center;
    cursor: pointer;
}
.rect01, .rect02, .rect03{
    transition: 0.5s ease;
}
.rect1:hover .rect01{
    transform: translateY(-12px) rotate(10deg);
}
.rect2:hover .rect02{
    transform: translateY(-12px) rotate(10deg);
}
.rect3:hover .rect03{
    transform: translateY(-12px) rotate(10deg);
}