/*------------------------------------NavBar--------------------------------------*/
.back-color{
  background-color: #1a1a1a;
}
.im-lo{
  height: 50px;
  width: 50px;
  object-fit: contain;
}
.sub-part{
  font-weight: 500;
  font-style: italic;
}
.navbar {
  display: flex;
  justify-content: space-between;
  background-color: #2b2b2b; 
  border-bottom: 2px solid #444; 
  align-items: center;
}

.nav-links {
  display: flex;
  list-style: none;
  gap: 12px;
}

.nav-buttons {
  margin-top: 18px;
  color: #e9e9e9; 
  background-color: transparent;
  border: none;
  font-size: 15px;
  cursor: pointer;
  transition: 0.5s ease;
}

.nav-buttons:hover {
  color: #cca028; 
  transform: scale(1.2);
}

.contact-info {
  margin-top: 18px;
  display: flex;
  margin-right: 10px;
  color: #dcdcdc;
}

.social-icons {
  display: flex;
  gap: 5px;
  color: #dcdcdc;
}

.ph-number {
  font-size: 15px;
}

.mobile-menu-icon {
  display: none;
  cursor: pointer;
}

.nav-links-mobile {
  display: none;
}

@media screen and (max-width: 768px) {
  .nav-links {
    display: none;
    list-style: none;
  }

  .mobile-menu-icon {
    display: block;
  }

  .nav-links-mobile {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 80px;
    right: 0;
    width: 100%;
    background-color: #2b2b2b;
    padding: 20px;
    border-top: 2px solid #444; 
  }

  .nav-buttons {
    margin: 10px 0;
  }
}

/*------------------------------------Slider--------------------------------------*/

.SliderBack {
  background-color: rgb(241, 241, 241);
}
.circle-icon-slide{
  display: flex;
  align-items: center;
  place-content: center;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: #c2c2c2;
}
.typing-text {
  font-family: 'Arial', sans-serif; 
  font-size: 20px; 
  margin-bottom: -13px;
  font-family: 'Arial Narrow Bold', sans-serif;
  color: #d6d6d6; 
  letter-spacing: 3px;
}

.welcom-title{
  font-size: 20px;
}
.home-title{
  color: #d3d3d3;
  padding-top: 5px;
}

.Sliderback {
  width: 100%;
  height: auto;
  object-fit: contain;
  background-color: #1a1a1a;
}

.slider-01-next {
  display: flex;
}

.slide-01 {
  display: grid;
  grid-template-columns: 1.2fr 1fr;
}

.slide-inner {
  align-self: flex-end;
  padding-left: 140px;
}

.im-slider {
  padding-right: 50px;
  height: 600px;
  width: 600px;
  object-fit: contain;
}

.social-icons-1 {
  margin-top: 20px;
  display: flex;
  gap: 5px;
}

.slide-CTA {
  background-color: #ca9400;
  color: #000000;
  border: 1px solid #ffffff;
  border-radius: 10px;
  height: 40px;
  width: 140px;
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
}

.slide-CTA-1 {
  background-color: transparent;
  color: #ffffff;
  font-weight: 500;
  border: 1px solid #ffffff;
  border-radius: 10px;
  height: 40px;
  width: 140px;
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
}

.slide-CTA-1:hover {
  background-color: #ca9400;
  color: #1b1b1b;
}

.slide-buttons {
  display: flex;
  gap: 10px;
}

.slide-UAE {
  color: #b38404;
  font-weight: 700;
}

.slide-01-para {
  color: #b3b3b3;
  text-align: justify;
}

.slide-01-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.slide-main-head {
  color: white;
  font-size: 35px;
}

.carousel-item {
  display: none;
  transition: opacity 1s ease-in-out;
  opacity: 0;
  height: 600px;
}

.carousel-item.active {
  display: block;
  opacity: 1;
}

.fade-in {
  animation: fadeIn 1s;
}

.fade-out {
  animation: fadeOut 1s;
}

@keyframes fadeIn {
  from {
    opacity: 0.5;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0.5;
  }
}

@media (max-width: 1200px) {
  .slide-01 {
    grid-template-columns: 1fr;
  }

  .slide-inner {
    padding-left: 40px;
  }

  .im-slider {
    padding: 0;
    width: 300px;
    height: 300px;
    object-fit: contain;
  }
}

@media (max-width: 768px) {

  .slide-01 {
    display: block;
  }

  .slide-inner {
    padding: 10px;
  }

  .slide-main-head {
    font-size: 28px;
  }

  .slide-01-para {
    font-size: 14px;
  }
  .slider-01{
    height: 1000px;
  }

  .im-slider {
    height: 300px;
    width: 100%;
  }

  .social-icons-1 {
    justify-content: flex-start;
  }

  .carousel-control-prev-icon,
  .carousel-control-next-icon {
    width: 30px;
    height: 30px;
  }
}

@media (max-width: 576px) {
  .slide-inner {
    padding-right: 10px;
    padding-left: 30px;
    margin: 30px;
  }
  .typing-text{
    font-size: 12px;
  }
  .slide-main-head {
    font-size: 22px;
  }

  .slide-01-para {
    font-size: 12px;
  }

  .im-slider {
    height: 250px;
    width: 100%;
    margin-top: -70px;
    object-fit: contain;
  }
  .SliderBack{
    background-color: #1a1a1a;
  }

  .slide-buttons {
    gap: 5px;
    margin-top: -10px;
    padding-right: 50px;
  }
  .social-icons-1{
    margin-top: 10px;
  }

  .slide-CTA,
  .slide-CTA-1 {
    width: 110px;
    height: 35px;
    font-size: 13px;
  }

  .carousel-control-prev-icon,
  .carousel-control-next-icon {
    width: 20px;
    height: 20px;
  }
}


