/*---------------------------------------Contact From-----------------------------------------*/
.Contact-back{
  background-color: #1a1a1a;
  display: flex;
  align-items: center;
  place-content: center;
  padding-top: 100px;
  padding-bottom: 100px;
  margin-top: -10px;
 
}
.contact-form {
  width: 100%;
  max-width: 550px;
  padding: 30px;
  background-color: #111111;
  border: 1px solid #474747;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.contact-form h2 {
  font-family: 'Poppins', sans-serif;
  color: #e0e0e0;
  margin-bottom: 20px;
}

.input-box {
  position: relative;
  margin-bottom: 20px;
  width: 350px;
  color: white;
}

.input-box input,
.input-box textarea {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  font-family: 'Poppins', sans-serif;
  color: #333;
  border: none;
  border-bottom: 2px solid #ca9400;
  background-color: transparent;
  outline: none;
  resize: none;
  transition: 0.3s ease;
}

.input-box textarea {
  height: 100px;
}

.input-box label {
  position: absolute;
  top: 10px;
  left: 10px;
  color: #888;
  pointer-events: none;
  transition: 0.3s ease;
}

.input-box input:focus ~ label,
.input-box input:valid ~ label,
.input-box textarea:focus ~ label,
.input-box textarea:valid ~ label {
  top: -12px;
  left: 0;
  color: #ca9400;
  font-size: 12px;
}

.submit-btn {
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  color: #fff;
  background-color: #ca9400;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-btn:hover {
  background-color: #b77900;
}



